@import url(https://fonts.googleapis.com/css2?family=Balsamiq+Sans:wght@400;700&family=Inconsolata:wght@400;700&display=swap);
body {
  margin: 0 auto;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.navbar {
  padding: 10px 20px;
  text-align: center;
  color: #ffffff;
  background-color: #66cc99;
  border-radius: 20px 20px 0 0;
  justify-content: center;
  align-items: center;
  font-family: "Balsamiq Sans", cursive;
}

.navbar h1 {
  margin: 10px 0;
  font-weight: 700;
  text-transform: uppercase;
  font-size: 2rem;
}

.navbar p {
  margin: 10px 0;
  font-size: 1.4rem;
  font-weight: 400;
}

.todo-list {
  margin: 20px;
}

.todo-list ul {
  font-family: "Inconsolata", monospace;
  font-weight: 400;
  font-size: 1.2rem;
  list-style: none;
  padding: 0;
}

.todo-list li {
  color: #353232;
  background-image: linear-gradient(#ccffcc, #ffff99);
  border-radius: 8px;
  cursor: pointer;
  padding: 10px;
  margin: 10px 0;
}

.todo-list li:hover {
  opacity: 0.5;
  text-decoration: line-through;
}

.empty {
  padding: 20px;
  font-family: "Inconsolata", monospace;
  font-weight: 800;
  color: #353232;
  font-size: 1.2rem;
}

.todo-form {
  padding: 20px;
  background: transparent;
  max-width: 90%;
}
label {
  font-family: "Inconsolata", monospace;
  font-size: 1.2rem;
  font-style: italic;
  margin-left: 0.5em;
  vertical-align: bottom;
}

input[type="text"] {
  max-width: 20em;
  background: transparent;
  border: none;
  border-bottom: 2px solid #00ccff;
  vertical-align: bottom;
}

input[type="text"]:focus,
input[type="text"]:active {
  background: transparent;
}

input[type="submit"] {
  font-size: 1.4rem;
  font-weight: 900;
  background-color: #66cc99;
  border: 2px solid #00cc99;
  border-radius: 50%;
  color: #ffffff;
  transition: 0.5s;
  cursor: pointer;
  margin-left: 0.5em;
}

input[type="submit"]:hover {
  background-color: #00ccff;
  border: 2px solid #0099ff;
}

.App {
  margin: 10px auto;
  max-width: 700px;
  width: 90%;
  text-align: center;
  justify-content: center;
  align-items: center;
  border: 5px solid #66cc99;
  border-radius: 26px;
  background-image: url(/final-todolist/static/media/page_bkgd.9d455a7d.svg);
  background-size: cover;
  background-repeat: repeat-y;
}

